import React, { useContext } from "react";
import { Switch, Route ,Redirect} from "react-router-dom";
import Products from "./products/Products";
import DetailProduct from "./detailProduct/DetailProduct";

import Login from "./auth/Login";
import Register from "./auth/Register";
import ForgotPassword from "./auth/ForgotPass";
import ResetPassword from "./auth/ResetPass";
import ActivationEmail from "./auth/Activation";
// import OrderHistory from "./history/OrderHistory";
import OrderDetails from "./history/OrderDetails";
import Cart from "./cart/Cart";
import NotFound from "./utlis/not_found/NotFound";
import Categories from "./categories/Categories";
import CreateProduct from "./createProduct/CreateProduct";
import Otpverify from "./auth/Otpverify";
import { GlobalState } from "../../GlobalState";
import Banner from "./banner/Banner";
import Worker from "./products/Worker";
import Catwork from './createCatwork/Catwork'
import CreateWorker from "./CreateWorker/CreateWorker";
import DetailWorker from "./detailWorker/DetailWorker";
import Address from "./cart/Address";
import AddAddress from "./cart/AddAddress";
import CheckOut from "./cart/Checkout";
import Status from "./status/Status";
import LoadProductsByCategory from "./products/LoadProductsByCategory";
import SearchResult from "./products/SearchResult";
import Contact from "./aboutUs/Contact";
import TermAndCondittion from "./aboutUs/TermAndCondittion";
import PrivatePolicy from "./aboutUs/PrivatePolicy";
import RefundPoilicy from "./aboutUs/RefundPoilicy";
import Offer from "./products/offer";
import LoadingNew from "./utlis/loading/LoadingNew";


function Pages() {
  const state = useContext(GlobalState);
  const [isLogged] = state.userAPI.isLogged;
  const [isAdmin] = state.userAPI.isAdmin;
  const [loading] = state.userAPI.loading;

  return (
    <Switch>
      <Route path="/" exact component={Products} />
      <Route path="/search-result"  component={SearchResult} />
      <Route path="/detail/:id/:cat_id" exact component={DetailProduct} />

      <Route path="/login" exact component={isLogged ? NotFound : Login} />
      <Route
        path="/register"
        exact
        component={isLogged ? NotFound : Register}
      />
      {/* <Route
        path="/verifyotp"
        exact
        component={isLogged ? NotFound : Otpverify}
      /> */}

      <Route
        path="/forgot_password"
        component={isLogged ? NotFound : ForgotPassword}
        exact
      />
      <Route
        path="/user/reset/:token"
        component={isLogged ? NotFound : ResetPassword}
        exact
      />

      <Route
        path="/user/activate/:activation_token"
        component={ActivationEmail}
        exact
      />

      <Route
        path="/category"
        exact
        component={isAdmin ? Categories : NotFound}
      />
      <Route path="/catwork" exact component={isAdmin ? Catwork : NotFound} />
      <Route
        path="/create_product"
        exact
        component={isAdmin ? CreateProduct : NotFound}
      />
      <Route
        path="/edit_product/:id"
        exact
        component={isAdmin ? CreateProduct : NotFound}
      />

      <Route
        path="/create_worker"
        exact
        component={isAdmin ? CreateWorker : NotFound}
      />
      <Route
        path="/edit_worker/:id"
        exact
        component={isAdmin ? CreateWorker : NotFound}
      />

    
      <Route
        path="/order_history"
        exact
        component={loading ? LoadingNew : (isLogged ?  OrderDetails: NotFound)}
      />

      <Route path="/cart" exact component={Cart} />
     
      <Route path="/address" exact component={isLogged ? Address : NotFound} />

      <Route
        path="/addaddress"
        exact
        component={isLogged ? AddAddress : NotFound}
      />
      <Route
        path="/banner"
        exact
        component={isLogged ? Banner : NotFound}
      />
      
      <Route path="/worker" exact component={Worker} />
      <Route path="/offer" exact component={Offer} />
      <Route path="/detailworker/:id" exact component={DetailWorker} />
      <Route
        path="/checkout"
        exact
        component={loading ? LoadingNew : (isLogged ? CheckOut : NotFound)}
      />
      <Route path="/status" exact component={isAdmin ? Status : NotFound} />
      <Route
        path="/load-product-by-category"
        exact
        component={LoadProductsByCategory}
      />
      <Route path="/contact" exact component={Contact} />
      <Route path="/termandcondition" exact component={TermAndCondittion} />
      <Route path="/privatepolicy" exact component={PrivatePolicy} />
      <Route path="/refundpolicy" exact component={RefundPoilicy} />

      <Redirect to='/'  />
      
    </Switch>
  );
}

export default Pages;
